"use client";
import React, { useState } from 'react';
import SimpleHeader from '@/components/SimpleHeader';
import { Mail, Phone, MapPin, Send } from 'lucide-react';
import { toast } from 'react-hot-toast';

export default function ContactPage() {
  const [formData, setFormData] = useState({ name: '', email: '', message: '' });

  const handleSubmit = (e) => {
    e.preventDefault();
    toast.success("Message sent! We'll get back to you soon.");
    setFormData({ name: '', email: '', message: '' });
  };

  return (
    <div className="min-h-screen bg-slate-50">
      <SimpleHeader />
      <main className="max-w-7xl mx-auto px-4 py-8 md:py-12">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
           
           {/* Contact Info */}
           <div className="flex flex-col justify-center">
              <h1 className="text-4xl font-bold text-slate-900 mb-6">Get in touch</h1>
              <p className="text-lg text-slate-600 mb-8">
                Have questions about the app? Suggestions for features? Or just want to say hi? We'd love to hear from you.
              </p>
              
              <div className="space-y-6">
                 <div className="flex items-start gap-4 p-6 bg-white rounded-2xl shadow-sm border border-slate-100">
                    <div className="bg-blue-50 p-3 rounded-xl text-blue-600"><Mail size={24} /></div>
                    <div>
                       <h3 className="font-bold text-slate-800">Email Us</h3>
                       <p className="text-slate-500">support@weatherwise.com</p>
                    </div>
                 </div>
                 
                 <div className="flex items-start gap-4 p-6 bg-white rounded-2xl shadow-sm border border-slate-100">
                    <div className="bg-green-50 p-3 rounded-xl text-green-600"><Phone size={24} /></div>
                    <div>
                       <h3 className="font-bold text-slate-800">Call Us</h3>
                       <p className="text-slate-500">+1 (555) 123-4567</p>
                    </div>
                 </div>

                 <div className="flex items-start gap-4 p-6 bg-white rounded-2xl shadow-sm border border-slate-100">
                    <div className="bg-purple-50 p-3 rounded-xl text-purple-600"><MapPin size={24} /></div>
                    <div>
                       <h3 className="font-bold text-slate-800">Visit Us</h3>
                       <p className="text-slate-500">123 Weather St, Cloud City</p>
                    </div>
                 </div>
              </div>
           </div>

           {/* Contact Form */}
           <div className="bg-white rounded-3xl shadow-lg border border-slate-100 p-8">
              <h2 className="text-2xl font-bold text-slate-800 mb-6">Send us a message</h2>
              <form onSubmit={handleSubmit} className="space-y-4">
                 <div>
                    <label className="block text-sm font-bold text-slate-700 mb-1">Name</label>
                    <input 
                      type="text" 
                      required
                      value={formData.name}
                      onChange={e => setFormData({...formData, name: e.target.value})}
                      className="w-full px-4 py-3 rounded-xl border border-slate-200 focus:ring-2 focus:ring-blue-500 outline-none bg-slate-50"
                      placeholder="Your name"
                    />
                 </div>
                 <div>
                    <label className="block text-sm font-bold text-slate-700 mb-1">Email</label>
                    <input 
                      type="email" 
                      required
                      value={formData.email}
                      onChange={e => setFormData({...formData, email: e.target.value})}
                      className="w-full px-4 py-3 rounded-xl border border-slate-200 focus:ring-2 focus:ring-blue-500 outline-none bg-slate-50"
                      placeholder="john@example.com"
                    />
                 </div>
                 <div>
                    <label className="block text-sm font-bold text-slate-700 mb-1">Message</label>
                    <textarea 
                      required
                      rows={4}
                      value={formData.message}
                      onChange={e => setFormData({...formData, message: e.target.value})}
                      className="w-full px-4 py-3 rounded-xl border border-slate-200 focus:ring-2 focus:ring-blue-500 outline-none bg-slate-50 resize-none"
                      placeholder="How can we help?"
                    ></textarea>
                 </div>
                 <button type="submit" className="w-full bg-blue-600 hover:bg-blue-700 text-white font-bold py-4 rounded-xl transition flex items-center justify-center gap-2">
                    Send Message <Send size={18} />
                 </button>
              </form>
           </div>
        </div>
      </main>
    </div>
  );
}